$desktop:1200px;
$labtop:1024px;
$tablet:768px;
$phone:480px;


.description-title{
    display: flex;
    justify-content: center;
    font-size: 32px;
    margin-top: 5%;
    color:  rgb(74, 118, 69);
}
.txt-type > .txt {
    border-right: 0.2rem solid #777;
}

.description {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4%;
    font-weight: 500;
    font-size: 20px;
    width: 60%;
    color:  rgb(74, 118, 69);
}

.description > span{
    opacity: 0;
    transition: 0.5;
}

.description > span.fade{
    opacity: 1;
}


.btn-slide-title{
    display: flex;
    justify-content: center;
    font-size: 32px;
    margin-top: 5%;
    color:  rgb(74, 118, 69);
    -webkit-animation-name: slide;
            animation-name: slide;
}

.btn-slide-title:hover{
    position: relative;
    -webkit-animation: slide 2s infinite alternate;
            animation: slide 2s infinite alternate;
    -webkit-animation-name: slide;
            animation-name: slide;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;  
}

@-webkit-keyframes slide {
   from {left: 0px}
   to {left: 50px}
}

@keyframes slide {
   from {left: 0px}
   to {left: 50px}
}

.btn-slide-set{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-between;
    margin-top: 5%;
    width: 60%;
}

.btn-1{
    position: relative;
    width: 140px;
    height: 40px;
    border-radius: 7px;
    font-size: 110%;
    background-color: #314D74;
    color: black;
       @media screen and (max-width:716px) {
        font-size: 100%;
    }
}

.btn-1::before {
    content: "";
    position: absolute;
    border-radius: 7px;
    top: -1px;
    left: -2px;
    bottom: -1px;
    right: -1px;
    z-index: -1;
    background-color: black;
    transition: transform 330ms ease-in-out;
    transform: scaleX(0);
    transform-origin: left;
}

.btn-1:hover::before,
.btn-1:focus::before {
    transform: scaleX(1);
}

.btn-1 {
    transition: color 330ms ease-in-out;
    z-index: 1;
}

.btn-1:hover,
.btn-1:focus{
    color: white
}

